import React from "react";
import { Link } from "gatsby";
import CallToAction from "../master/cta";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import email from "../../assets/images/contactUs/mail.svg";
import phone from "../../assets/images/contactUs/call.svg";
import fax from "../../assets/images/contactUs/directbox-default.svg";
import office from "../../assets/images/contactUs/location.svg";

import "./contactUs.css";

const ContactUs = () => {
  return (
    <div className='home-content'>
      <div className='header-title'>
        <div className='container'>
          <div className='header-title-text'>
            <h2>
              Contact <span>us</span>
            </h2>
            <ul>
              <li>
                <Link to='/'>Home</Link>
              </li>

              <li>/ Contact Us</li>
            </ul>
          </div>
        </div>
      </div>
      <div className='contactUs-section'>
        <div className='container'>
          <div className='contactUs-grid-section'>
            <div className='contactUsPage-header'>
              <h3>Contact Us</h3>
              <p>
                Talk to one of our team members and we will help you find what
                you need.
              </p>
            </div>
            <div className='contactUsPage-content'>
              <div className='contactUsPage-contentDetails'>
                <div className='contactUsPage-box'>
                  <div className='contactUsPage-imgbox'>
                    <img src={email} alt='email' />
                  </div>
                  <h3>Email</h3>
                  <p> admin@imgo.com.au</p>
                </div>
                <div className='contactUsPage-box'>
                  <div className='contactUsPage-imgbox'>
                    <img src={phone} alt='phone' />
                  </div>
                  <h3>Phone</h3>
                  <p> 07 3219 8882</p>
                </div>
                <div className='contactUsPage-box'>
                  <div className='contactUsPage-imgbox'>
                    <img src={fax} alt='fax' />
                  </div>
                  <h3>Fax</h3>
                  <p>3219 8884</p>
                </div>
                <div className='contactUsPage-box'>
                  <div className='contactUsPage-imgbox'>
                    <img src={office} alt='office' />
                  </div>
                  <h3>Store Location</h3>
                  <p>7-9 Eileen St, Underwood QLD 4119, Australia.</p>
                </div>
              </div>
              <div className='contactUsPage-form'>
                <Form>
                  <Form.Group className='InputGroup'>
                    <Form.Label>Name</Form.Label>
                    <Form.Control type='text' placeholder='John Deo' />
                  </Form.Group>
                  <Form.Group className='InputGroup'>
                    <Form.Label>Phone Number</Form.Label>
                    <Form.Control type='number' placeholder='123456789' />
                  </Form.Group>
                  <Form.Group className='InputGroup'>
                    <Form.Label>Email Address</Form.Label>
                    <Form.Control
                      type='email'
                      placeholder='username@email.com'
                    />
                  </Form.Group>
                  <Form.Group className='InputGroup'>
                    <Form.Label>Message</Form.Label>
                    <Form.Control
                      as='textarea'
                      rows={3}
                      placeholder='Leave your Enquiriy here...'
                    />
                  </Form.Group>
                  <Button variant='primary' type='submit'>
                    Send
                  </Button>
                </Form>
              </div>
            </div>
          </div>
        </div>
      </div>
      <CallToAction />
    </div>
  );
};

export default ContactUs;
